import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import {FormsModule} from "@angular/forms";
import {MaterialModule} from "./shared/material/material.module";
import {SharedModule} from "./shared/shared/shared.module";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {MainComponent} from "./shared/shared/main/main.component";
import {BACKEND_URL, HEROKU_SERVER_URL, JSON_SERVER_URL, SPRING_SERVER_URL} from "./app.tokens";
import {ServiceModule} from "./service/service.module";
import {RoutingModule} from "./routing.module";
import {ContactModule} from "./contact/contact.module";
import {FlexLayoutModule} from "@angular/flex-layout";
import {LanguageModule} from "./language/language.module";
import {FirstStepModule} from "./examples/first-step/first-step.module";


@NgModule({
  declarations: [
    AppComponent,
    MainComponent
  ],
  imports: [
    /*Application Module*/
    LanguageModule,
    SharedModule,
    ServiceModule,
    MaterialModule,
    RoutingModule,
    ContactModule,
    FirstStepModule,
    /*Core Module*/
    FlexLayoutModule,
    BrowserAnimationsModule,
    BrowserModule,
    FormsModule,
  ],
  providers: [
    {provide: BACKEND_URL, useValue: HEROKU_SERVER_URL},
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



