import {Injectable} from "@angular/core";
import {WebPage} from "../model/WebPage.model";
import {RestDatasource} from "./rest-datasource.service";
import {Category} from "../model/Category.model";
import {Subject} from "rxjs/Subject";
import {Observable} from "rxjs/Observable";
import {Topic} from "../model/Topic.model";

@Injectable()
export class CategoryRepository {
  readonly URL: string = "category";

  constructor(private dataSource: RestDatasource){
  }

  getByWebPage(webPage: WebPage): Observable<Category> {
    return this.dataSource.get(this.URL)
      .switch()
      .filter((category: Category) => {
        return category.webPage.name.toUpperCase() == webPage.name.toUpperCase();
        });
  }

  getCategory(id: number): Observable<Category>{
    return this.dataSource.get(this.URL)
      .switch()
      .filter((_category: Category) => {
        return _category.id == id;
      });
  }

  getCategories(): Observable<Category[]> {
    return this.dataSource.get(this.URL);
  }

  getCategoryByTopic(topic: Topic): Observable<Category> {
    return this.dataSource.get(this.URL + "/topic/" + topic.id);
  }
}



