import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {GreetingPopup, WelcomeMsgComponent} from './welcome-msg/welcome-msg.component';

import {FormsModule} from "@angular/forms";
import { SelectLanguageComponent } from './welcome-msg/select-language/select-language.component';
import {I18nSupportService} from "./service/i18n-support.service";
import {MAT_DIALOG_DEFAULT_OPTIONS} from "@angular/material";
import {MaterialModule} from "../../../shared/material/material.module";

@NgModule({
  imports: [
    CommonModule,
    MaterialModule,
    FormsModule,
  ],
  providers: [
    I18nSupportService,
    {provide: MAT_DIALOG_DEFAULT_OPTIONS, useValue: {hasBackdrop: false}}
  ],
  declarations: [
    WelcomeMsgComponent,
    SelectLanguageComponent,
    GreetingPopup,
  ],
  entryComponents: [GreetingPopup]
})
export class FirstStepChapter1Module{}



