import {AfterViewInit, Component, Input, OnInit, ViewChild} from '@angular/core';
import {MatSort, MatTableDataSource} from "@angular/material";
import {CategoryRepository} from '../../service/category-repository.service';
import {Category} from "../../model/Category.model";
import {WebPage} from '../../model/WebPage.model';

@Component({
  selector: 'sw-category-table',
  templateUrl: './category-table.component.html',
  styleUrls: ['./category-table.component.css']
})
export class CategoryTableComponent implements OnInit{
  display = [ 'id','name','level','updated', 'url'];
  dataSource;
  categories: Category[] = [];
  @Input() webPage: WebPage;


  constructor(private categoryRepository: CategoryRepository) {}

  ngOnInit() {
    this.categoryRepository.getByWebPage(this.webPage)
      .subscribe(data => {
        this.categories.push(data);
        this.dataSource = new MatTableDataSource(this.categories);
      });
  }


  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

}



