import {Injectable} from "@angular/core";
import {RestDatasource} from "./rest-datasource.service";
import {Subject} from "rxjs/Subject";
import {WebPage} from "../model/WebPage.model";
import {Observable} from "rxjs/Observable";
import 'rxjs/Rx';
@Injectable()
export class WebpageRepository {
  readonly URL: string = 'webpage';

  constructor(private dataSource: RestDatasource){}

  getByName(name: string): Observable<WebPage>{
    return this.dataSource.get(this.URL)
      .switch()
      .filter((_webPage:WebPage) => {
      return _webPage.name.toUpperCase() == name.toUpperCase();
    })
  }

  getNames(): Observable<string> {
    return this.dataSource.get(this.URL)
      .switch()
      .map((_webpage: WebPage) => _webpage.name);
  }

}



