import { Component, OnInit } from '@angular/core';
import {NavigationEnd, Router} from "@angular/router";

@Component({
  selector: 'sw-locator',
  templateUrl: './locator.component.html',
  styleUrls: ['./locator.component.css']
})
export class LocatorComponent implements OnInit {
  urlSegments: string[] = [];
  constructor(private router: Router) { }

  ngOnInit() {
    this.router
      .events
      .filter(event => event instanceof NavigationEnd)
      .subscribe((event: NavigationEnd) => {
        this.urlSegments = [];
        let url = event.url;
        this.extractSegments(url);
      })
  }

  extractSegments(url: string) {
    url.split("/").forEach(_segment => {
      if(_segment.includes("?")) {
        _segment = _segment.substr(0,_segment.indexOf("?"));
      }
      if(_segment != null && _segment != "") {
        this.urlSegments.push(decodeURI(_segment));
      }
    });
  }

}



