import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Params, Router} from "@angular/router";
import {Category} from "../../model/Category.model";
import {CategoryRepository} from "../../service/category-repository.service";
import {Topic} from "../../model/Topic.model";
import {TopicRepository} from "../../service/topic-repository.service";

@Component({
  selector: 'app-category-main',
  templateUrl: './category-main.component.html',
  styleUrls: ['./category-main.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class CategoryMainComponent implements OnInit {
  categoryId: number;
  category: Category;
  topicId: number;
  topic: Topic;
  isOverview: boolean = false;


  constructor(private route: ActivatedRoute,
              private categoryRepository: CategoryRepository,
              private topicRepository: TopicRepository) {

    this.route.queryParams.subscribe((params: Params) => {
      console.log(params);
      this.categoryId = params['categoryId'];
      if(this.categoryId) {
        this.isOverview = true;
        this.categoryRepository.getCategory(this.categoryId).subscribe((_category: Category) => {
          this.category = _category;
          console.log(this.category);
        });
      } else {
        this.isOverview = false;

        this.topicId = params['topicId'];
        this.topicRepository.getTopic(this.topicId).subscribe((_topic:Topic) => {
          this.topic = _topic;
          console.log(this.topic);
          this.categoryRepository.getCategoryByTopic(this.topic).subscribe(_category => {
            this.category = _category;
          })
        });
      }

    })
  }

  ngOnInit() {
  }

}



