import {Component, Input, OnInit} from '@angular/core';
import {CategoryRepository} from "../../service/category-repository.service";
import {WebPage} from "../../model/WebPage.model";
import {Category} from "../../model/Category.model";
import {TopicRepository} from "../../service/topic-repository.service";
import {Topic} from "../../model/Topic.model";

@Component({
  selector: 'sw-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.css']
})
export class CategoryListComponent implements OnInit {
  @Input() webPage: WebPage;
  categories: Category[] = [];
  panelOpenState: boolean = false;

  constructor(private categoryRepository: CategoryRepository,
              private topicRepository: TopicRepository) {
  }

  ngOnInit() {
    this.categoryRepository.getByWebPage(this.webPage)
      .subscribe((_category: Category) => {
        this.categories.push(_category);
        this.categories.forEach((_category: Category)=> {
          this.topicRepository.getTopicsByCategory(_category).subscribe((_topics: Topic[]) => {
            _category.topics = _topics;
          });
        })
      });
  }

}



