import { NgModule } from '@angular/core';
import {HttpModule} from "@angular/http";
import {RestDatasource} from "./rest-datasource.service";
import {WebpageRepository} from "./webpage-repository.service";
import {CategoryRepository} from "./category-repository.service";
import {LocaleResolver} from "./locale-resolver.service";
import {TopicRepository} from "./topic-repository.service";

@NgModule({
  imports: [HttpModule],
  providers: [
    RestDatasource,
    TopicRepository,
    WebpageRepository,
    CategoryRepository,
    LocaleResolver,
  ]
})
export class ServiceModule { }



