import {
  AfterViewInit,
  Component,
  ElementRef, Inject,
  ViewChild
} from '@angular/core';
import 'rxjs/Rx';
import {MAT_DIALOG_DATA, MatDialog, MatExpansionPanel} from "@angular/material";
import {I18nSupportService} from "../service/i18n-support.service";

@Component({
  selector: 'sw-welcome-msg',
  templateUrl: './welcome-msg.component.html',
  styleUrls: ['./welcome-msg.component.css'],

})
export class WelcomeMsgComponent implements AfterViewInit {
  userName: string;
  valid: boolean = false;
  code: string = 'NOT_SELECTED';

  private static LIMIT_TIME_INPUT = 5000;
  @ViewChild('panel') expansionPanel: MatExpansionPanel;
  @ViewChild('nameInput') nameInput: ElementRef;

  constructor(public i18nSupportService: I18nSupportService,
              public dialog: MatDialog) {
    this.i18nSupportService.changeCode$.subscribe(_language => {
      this.code = _language.code;
      this.toggleColor();
    })
  }

  setName(name: string) {
    this.userName = name;
  }

  _toggled: boolean = false;
  _color = this.toggleColor();
  toggleColor() {
    let color;
    if(this._toggled) {
      color = 'primary';
    } else {
      color = 'accent';
    }
    this._toggled = !this._toggled;
    this._color = color;
  }
  ngAfterViewInit() {
    // let input$: Observable<any> = Observable.fromEvent(this.nameInput.nativeElement,'keyup');
    // input$.map(event => event.target.value).subscribe(value => {
    //   this.valid = this.validName(value);
    //   console.log(this.valid);
    // });

    setTimeout(()=>{
      if(this.validName()) {
        return;
      } else {
        this.expansionPanel.expanded = true;
      }
    },WelcomeMsgComponent.LIMIT_TIME_INPUT);
  }

  validName(): boolean {
    return this.userName != null && this.userName.length > 3;
  }

  openDialog() {
    this.i18nSupportService
      .getLocaleMessage(this.userName == undefined ? 'Guest' : this.userName )
      .subscribe(
      greeting => {
        this.dialog.open(GreetingPopup, {
          data: {
            greeting: greeting,
          },
          width: '200px'
        });
      }
    )
  }
}

@Component({
  selector: 'sw-greeting-popup',
  template: `
    <div class="container"
         fxLayoutGap="5px"
         fxLayout="column" 
         style="text-align: center"
         fxLayoutAlign="center center">
      <h2 fxFlex="1" mat-dialog-title>
        {{ data.greeting }}!
      </h2>
      <mat-divider [inset]="true"></mat-divider>
      <mat-dialog-actions fxFlex="1">
        <button mat-raised-button color="primary" [mat-dialog-close]="true">Close</button>
      </mat-dialog-actions>
    </div>
  `
})
export class GreetingPopup{
  constructor(@Inject(MAT_DIALOG_DATA) public data: any) { }
}



