import {Injectable} from "@angular/core";
import {LANG_METADATA} from "../lang-metadata";
import {Observable} from "rxjs/Observable";
import {Subject} from "rxjs/Subject";
import 'rxjs/Rx';
@Injectable()
export class I18nSupportService {
  _currentCode: string = 'ko';
  changeCode$: Subject<any> = new Subject<any>();
  constructor() {
  }

  set currentCode(code: string) {
    this.changeCode$.next(LANG_METADATA.find(_language => _language.code === this.currentCode));
    this._currentCode = code;
  }

  get currentCode() {
    return this._currentCode;
  }
  getLocaleMessage(userName: string): Observable<string> {
    return Observable
      .of(LANG_METADATA.find(_language => _language.code === this.currentCode))
      .map(_language =>{
        console.log(_language);
        return  `${_language.msg}, ${userName}`
      })
      .first();

  }
}



