import { NgModule } from '@angular/core';

import {FlexLayoutModule} from "@angular/flex-layout";
import {
  MatButtonModule,
  MatFormFieldModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatMenuModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatSelectModule,
  MatExpansionModule,
  MatSidenavModule,
  MatTableModule,
  MatTabsModule,
  MatToolbarModule,
  MatTooltipModule, MatDividerModule, MatCardModule, MatChipsModule, MatDialogModule
} from "@angular/material";

const materials = [
  FlexLayoutModule,
  MatProgressSpinnerModule,
  MatSelectModule,
  MatTableModule,
  MatTabsModule,
  MatExpansionModule,
  MatProgressBarModule,
  MatSidenavModule,
  MatButtonModule,
  MatToolbarModule,
  MatTooltipModule,
  MatListModule,
  MatFormFieldModule,
  MatInputModule,
  MatMenuModule,
  MatDividerModule,
  MatCardModule,
  MatIconModule,
  MatDialogModule,
  MatChipsModule,
];

@NgModule({
  imports: materials,
  exports: materials,
})
export class MaterialModule { }



