import { Component, OnInit } from '@angular/core';
import {WebPage} from "../../model/WebPage.model";
import {WebpageRepository} from "../../service/webpage-repository.service";

@Component({
  selector: 'app-language-default',
  templateUrl: './language-default.component.html',
  styleUrls: ['./language-default.component.css']
})
export class LanguageDefaultComponent implements OnInit {
  readonly WEBPAGE_NAME: string = 'language';
  dataLoaded: boolean = false;
  webPage: WebPage;

  constructor(public webPageRepository: WebpageRepository) {
  }

  ngOnInit(): void {
    this.dataLoaded = false;
    this.webPageRepository.getByName(this.WEBPAGE_NAME).subscribe(_webPage => {
      this.webPage = _webPage;
      this.dataLoaded = true;
    });
  }

}



