import {Injectable} from "@angular/core";
import {Category} from "../model/Category.model";
import {Observable} from "rxjs/Observable";
import {Topic} from "../model/Topic.model";
import {RestDatasource} from "./rest-datasource.service";

@Injectable()
export class TopicRepository {

  constructor(private dataSource: RestDatasource){}

  getTopics(): Observable<Topic[]> {
    return this.dataSource.get('topic');
  }

  getTopicsByCategory(category: Category): Observable<Topic[]> {
    if(category == null) return null;
    return this.dataSource.get(`topic/category/${category.id}`);
  }

  getTopicByName(param) {
    return undefined;
  }

  getTopic(topicId: number) {
    return this.dataSource.get(`topic/${topicId}`).first();
  }
}



