import { Component, OnInit } from '@angular/core';
import {I18nSupportService} from "../../service/i18n-support.service";
import {LANG_METADATA} from "../../lang-metadata";

@Component({
  selector: 'sw-select-language',
  templateUrl: './select-language.component.html',
  styleUrls: ['./select-language.component.css']
})
export class SelectLanguageComponent implements OnInit {
  langMetaData = LANG_METADATA;
  langCode: string;
  constructor(public i18nSupportService: I18nSupportService) {
    this.langCode = this.i18nSupportService.currentCode;
  }

  ngOnInit() {
  }

}



