import {Component, OnInit} from '@angular/core';
import {WebpageRepository} from "../../../service/webpage-repository.service";
import {Subscription} from "rxjs/Subscription";
import {WebPage} from "../../../model/WebPage.model";
import {init} from "protractor/built/launcher";

@Component({
  selector: 'sw-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit{
  readonly WEBPAGE_NAME: string = 'main';
  dataLoaded: boolean = false;
  webpage: WebPage;

  constructor(public webPageRepository: WebpageRepository) {}

  ngOnInit(): void {
    this.webPageRepository.getByName(this.WEBPAGE_NAME).subscribe(_webPage => {
      this.webpage= _webPage;
      this.dataLoaded = true;
    });
  }
}



