import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HeaderComponent } from './header/header.component';
import {MaterialModule} from "../material/material.module";
import { WebpageComponent } from './main/webpage/webpage.component';
import { SpinnerComponent } from './spinner/spinner.component';
import {RouterModule} from "@angular/router";
import { LocatorComponent } from './header/locator/locator.component';

@NgModule({
  imports: [
    CommonModule,
    MaterialModule,
    RouterModule,
  ],
  declarations: [
    HeaderComponent,
    WebpageComponent,
    SpinnerComponent,
    LocatorComponent,
  ],
  exports: [
    HeaderComponent,
    WebpageComponent,
    SpinnerComponent,
    MaterialModule,
  ]
})
export class SharedModule { }



