import { Component, OnInit } from '@angular/core';
import {
  ActivatedRoute,
  UrlSegment
} from "@angular/router";

@Component({
  selector: 'sw-first-step-bootstrap',
  templateUrl: './first-step-bootstrap.component.html',
  styleUrls: ['./first-step-bootstrap.component.css']
})
export class FirstStepBootstrapComponent implements OnInit {
  paths: string;
  constructor(private route: ActivatedRoute) {
    route.url.subscribe((url: UrlSegment[]) => {
      url.forEach(segment => this.paths= "/" +segment);
    })
  }

  ngOnInit() {
  }

}



