import {Component, EventEmitter, OnDestroy, OnInit, Output} from '@angular/core';
import {WebpageRepository} from "../../../service/webpage-repository.service";
import {MatIconRegistry} from "@angular/material";
import {DomSanitizer} from "@angular/platform-browser";
import {Subscription} from "rxjs/Subscription";

@Component({
  selector: 'sw-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit{
  @Output() onToggleSidenav: EventEmitter<void> = new EventEmitter<void>();
  webpageNames: string[] = [];

  constructor(private webpageRepository: WebpageRepository) {}

  ngOnInit(): void {
    this.webpageRepository.getNames().subscribe(name => {
      this.webpageNames.push(name);
    })
  }

  toggleSidenav() {
    this.onToggleSidenav.emit();
  }
}



