import {NgModule} from "@angular/core";
import {RouterModule, Routes} from "@angular/router";
import {FirstStepBootstrapComponent} from "./first-step-bootstrap/first-step-bootstrap.component";
import {WelcomeMsgComponent} from "./chapter1/welcome-msg/welcome-msg.component";

const routes: Routes = [
  {
    path: 'first-step', component: FirstStepBootstrapComponent, children: [
      { path: '', redirectTo: 'chapter1', pathMatch: 'full' },
      { path: 'chapter1', component: WelcomeMsgComponent }
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FirstStepRoutingModule{}



