import { NgModule } from '@angular/core';
import {RouterModule, Routes} from "@angular/router";
import {MainComponent} from "./shared/shared/main/main.component";
import {ContactComponent} from "./contact/contact/contact.component";
import {LanguageMainComponent} from "./language/language-main/language-main.component";
import {LanguageDefaultComponent} from "./language/language-default/language-default.component";
import {CategoryMainComponent} from "./language/category-main/category-main.component";

const routes: Routes = [
  {path: 'main', component: MainComponent},
  {path: 'MAIN', redirectTo: 'main'},
  {path: '', redirectTo: 'main', pathMatch: 'full'},
  {path: 'contact', component: ContactComponent},
  {path: 'language', component: LanguageMainComponent, children: [
      {path: '', component: LanguageDefaultComponent, pathMatch: 'full'},
      {path: 'topic/:topicName', component: CategoryMainComponent },
      {path: ':categoryId', component: CategoryMainComponent },
      {path: '**', redirectTo: ''}
    ]},
  {path: 'LANGUAGE', redirectTo: 'language'},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class RoutingModule { }



